   const $=require("webpack-zepto");
   const Zepto=$;
   const touch=require('./touch.js');
   const music=require('./music.js');
   const share=require('./share.js');


   var h5={
                qid:1,
                qnext:function(){
                    if(this.qid<4){
                        $(".tik"+this.qid).addClass("hide");
                        this.qid++;
                        $(".tik"+this.qid).removeClass("hide");
                    }else{
                        $(".tk").addClass("hide");
                        var r=h5.makeRandom(1,3);
                        if(r!=3){
                            $(".rstimg").attr("src","img/rst"+r+".png");
                        }else{
                            $(".rstimg").attr("src","img/rst"+r+".png").css("top",'135px');
                        }
                        
                        $(".resultbox").removeClass("hide");
                    }
                },
                makeRandom:function(min,max){
                    var c=max-min+1;
                    return Math.floor(Math.random()*c+min);
                },
                jpid:"",
                isCanCj:function(){
                    //   
                    if(localStorage.getItem("isget_201803jindi_v3")==null){
                        $.ajax({
                            url:"ajax/sendAjax.php",
                            data:{'uParameter':'getJPlist'},
                            type:"post",
                            dataType:"json",
                            success:function(data){
                                console.log(data);
                                if(data.getJP_msg=='1'){
                                    //
                                    h5.jpid=data.jp_Id;
                                    localStorage.setItem("isget_201803jindi_v3","geted");
                                    h5.jpnames=data.getJP_name;
                                    if(data.getJP_name==''){
                                        $(".rst_jp").attr("src","img/jp1.png");
                                    }else if(data.getJP_name==''){
                                         $(".rst_jp").attr("src","img/jp2.png");
                                    }else if(data.getJP_name==''){
                                         $(".rst_jp").attr("src","img/jp3.png").css({"left":"196px","top":"230px"});
                                    }
                                    h5.toPage(4);
                                }else{
                                    //
                                    // alert("");
                                    //localStorage.setItem("isget_201803jindi_v3","geted");
                                    $(".sharebox").removeClass("hide").siblings(".tk").addClass("hide"); 
                                    return false;
                                }
                            }
                        });
                    }else{
                        alert("");
                        //window.location.reload();
                        $(".sharebox").removeClass("hide").siblings(".tk").addClass("hide"); 
                        return false;
                    }
                },
                jpnames:'',
                sendInfo:function(game_name,game_phone){
                    //
                    $.ajax({
                        url:"ajax/sendAjax.php",
                        data:{
                            'uParameter':'geted_Info_send',
                            'cj_name':game_name,
                            'cj_phone':game_phone,
                            'jptypes':h5.jpnames,
                            'cj_jpid':h5.jpid
                        },
                        type:"post",
                        dataType:"json",
                        success:function(data){
                            console.log(data);
                            if(data.msg=="1"){
                                localStorage.setItem("isget_201803jindi_v3","geted");
                                alert("");
                                window.location.reload();
                                //h5.next();
                            }else if(data.msg=="3"){
                                localStorage.setItem("isget_201803jindi_v3","geted");
                                alert("");
                                return false;
                                //window.location.reload();
                            }else{
                                alert("");
                                localStorage.clear();
                                window.location.reload();
                            }
                        }
                    });
                },
                num: 1,
                total : $(".page").length,                
                initHtml : function (){    //class
                   // var AnimateImg = $($(".page" + this.num)).find(".animated");
                    var AnimateImg = $(".page" + this.num).find(".animated");
                    $(AnimateImg).each(function(index, value){
                        var animationStyle = $(this).data("class");
                        $(this).removeClass(animationStyle);
                    })  
                },
                next : function(){
                    if(this.num < this.total){
                        $(".page" + this.num).addClass("fadeOut hide");
                        this.initHtml();
                        this.num++;
                        this.setAnimation(this.num);
                    }
                },
                last : function(){
                    if(this.num > 1){
                        $(".page" + this.num).addClass("fadeOut hide");
                        this.initHtml();
                        this.num--;
                        this.setAnimation(this.num);
                    }
                },          
                setAnimation : function(pageNum){
                    $(".page" + pageNum).removeClass("fadeOut hide");
                    var AnimateImg = $(".page" + pageNum).find(".animated");
                    $(AnimateImg).each(function(index, value){
                        var animationStyle = $(this).data("class");
                        var animationDelay = $(this).data("delay");
                        $(this).addClass(animationStyle).css({
                            "animation-delay":animationDelay,
                            "-webkit-animation-delay":animationDelay
                        });
                    })  
                },                            
                loadImg : function(){
                    var totalNum = 0;
                    var jiazai = 0;
                    var imgs = document.getElementsByTagName("img");
                    var imgLen = imgs.length;        
                    function setBaiNum(bai){
                        $("#baifen_txt").text(bai);
                        if(bai == 100){
                            setTimeout(function(){
                                $(".p1_close").addClass('hide');  
                                $(".pl_bj_d").addClass('hide');                              
                                h5.setAnimation(1);
                                
                            }, 500);
                            
                        }
                    }
                    for(var i = 0; i < imgLen; i++){
                        if(imgs[i].complete){
                            jiazai++;
                            var baifenbi = Math.floor(jiazai/imgLen*100);
                            setBaiNum(baifenbi);
                            continue;
                        }
                        imgs[i].onload = function(){
                            jiazai++;
                            var baifenbi = Math.floor(jiazai/imgLen*100);
                            setBaiNum(baifenbi);
                            
                        }
                    }
                },
                IsPC : function (){
                    var userAgentInfo = navigator.userAgent;
                    var Agents = ["Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod"];
                    var flag = true;
                    for (var v = 0; v < Agents.length; v++) {
                        if (userAgentInfo.indexOf(Agents[v]) > 0) {
                            flag = false;
                            break;
                        }
                    } 
                    return flag;  
                },
                toPage: function(num){
                    $(".page" + this.num).addClass('hide');
                    //h5.initHtml(this.num-1, $(".page" + this.num));
                    h5.initHtml();
                    h5.setAnimation(num);
                    h5.num = num;
                },
                viewResize : function(){
                    var screen_w=parseInt(window.screen.width),scale=screen_w/750;
                    $("#auto_view").attr("content","width=750, initial-scale = "+scale+",user-scalable=1, minimum-scale = "+scale+", maximum-scale = "+scale);
                    if(h5.IsPC()){
                        document.body.style.margin = "0px auto";
                    }
                },
                window_viewResize:function(){
                        window.onresize = function(){
                            h5.viewResize();
                        }
                },
                AnimationEnd : function(div, style){
                    $(document).on("webkitAnimationEnd", div, function(e){
                        // e.target.oh5setParent.classList.add("pulse", "infinite");
                        $(div + "_parent").addClass(style+" infinite");
                    })                    
                },
                makePage:function(){
                    window.onload = function(){
                        document.addEventListener('touchmove', function (event) {
                            if(h5.num!=5){event.preventDefault();}
                        }, false);  
                        var firstTouch = true; //
                        $('body').bind("touchstart",function(e){
                            if (firstTouch) { 
                                firstTouch = false; 
                                document.getElementById('theaudio').play();
                            }else{
                                return; 
                            } 
                        }); 

                        $(document).on("touchend",".close",function(){
                            h5.toPage(3);
                        });

                        $(document).on("touchend",".p43",function(){
                            //
                            h5.isCanCj();
                        });

                        $(document).on("touchend",".p42",function(){
                            h5.toPage(5);
                        });

                        $(document).on("touchend",".p23",function(){
                            h5.next();
                        });

                        $(document).on("touchend",".choose",function(){
                            h5.qnext();
                        });
               
                        $(".page1").swipeUp(function(){ 
                            h5.next();                
                        });
                        $("body").swipeDown(function(){ 
                            h5.last();                
                        });
                        h5.AnimationEnd(".p23","pulse");
                    }
                }
   } 

    module.exports = {
            h5          : h5,
            load        : h5.loadImg(),
            viewResize  : h5.window_viewResize(),
            makePage    : h5.makePage()
    };
  